<template>
  <div class="detail">
      <note
              :des="this.$route.query.des"
              :author="product.author"
              :avatar="product.avatar"
              :time="product.time"
              :detail="product.detail"
              :loading="loading"
      ></note>
  </div>
</template>

<script>
    import Note from '../components/Note'
    export default {
        name: 'detail',
        components: {
          'note': Note
        },
        data() {
            return {
                product: {},
                loading: true
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                this.$ajax.get(`https://api.github.com/gists/${this.$route.query.detailCode}`).then((res)=>{
                    this.product = JSON.parse(res.data.files.product.content);
                    this.loading = false
                })
            }
        }
    }
</script>


<style lang="scss" scoped>
    .detail {
        position: absolute;
        top: 46px;
        left: 0;
        right: 0;
        bottom: 0;

        overflow: auto;
        -webkit-overflow-scrolling: touch;
    }
</style>