<template>
    <div class="note" :class="{'note-loading': loading}">
        <header class="note-header" >
            <div class="note-header-top">
                {{des}}
            </div>
            <div class="note-header-bottom">
                <a class="note-header-bottom-left">
                    <img v-if="!loading" class="note-avatar" :src="avatar"/>
                </a>
                <div class="note-header-bottom-right">
                    <p class="note-author">{{author}}</p>
                    <p class="note-time">{{time}}</p>
                </div>
            </div>
        </header>
        <section class="note-section">
            <div class="note-section-top">
                {{detail}}
            </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "note",
        props: {
            avatar: {
                type: String,
                default: ''
            },
            author: {
                type: String,
                default: ''
            },
            time: {
                type: String,
                default: ''
            },
            des: {
                type: String,
                default: ''
            },
            imgs: {
                type: Array,
                default: () => ['','','']
            },
            visited: {
                type: Number,
                default: 0
            },
            note: {
                type: String,
                default: ''
            },
            detail: {
                type: String,
                default: ''
            },
            loading: {
                type: Boolean,
                default: false
            }
        }
    }
</script>

<style lang="scss" scoped>
    .note {
    .note-header {
        margin: 0 15px;
    .note-header-top {
        margin-top: 15px;

        font-size: 30px;
        font-weight: 700;
        color: #333;
    }
    .note-header-bottom {
        margin-top: 15px;
        display: flex;
        align-items: center;
    .note-header-bottom-left {
        width: 50px;
        height: 50px;

        border-radius: 50%;
        border: 1px solid #eee;

        font-size: 0;

        overflow: hidden;

    .note-avatar {
        width: 100%;
        height: 100%;
    }
    }
    .note-header-bottom-right {
        display: flex;
        flex: 1;
        justify-content: space-between;
        flex-direction: column;

        margin: 0 5px;

    .note-author {
        margin-bottom: 10px;

        min-height: 25px;

        font-size: 22px;

        color: #333;
    }

    .note-time {
        min-height: 16px;

        font-size: 14px;

        color: #999;
    }
    }
    }
    }
    .note-section {
        margin: 0 15px;
    .note-section-top {
        margin-top: 15px;
        line-height: 26px;

        color: #222;
    }
    }
    &.note-loading {

    .note-header-top,
    .note-header-bottom-left,
    .note-author,
    .note-time {
        background-color: #eee;
    }
    .note-header-top {
        color: transparent;
    }
    .note-author,
    .note-time {
        transform-origin: top left;
    }
    .note-author {
        animation: loading 1s ease-in-out infinite;
    }
    .note-time {
        animation: loading 1s ease-in-out -.5s infinite;
    }
    @keyframes loading {
        0% {
            transform: scaleX(0.4);
        }

        50% {
            transform: scaleX(0.8);
        }
        100% {
            transform: scaleX(0.4);
        }
    }
    }
    }
</style>